.app-wrapper {
  margin: 0 auto;

  display: grid;

  width: 1200px;


  grid-template-areas:
  "h h"
  "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 200px 2fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  grid-area: c;

  background-color: rgba(255, 103, 0, 0.5);
}



